                            <div class="mr-2 d-inline-block">
                                <span class="legends_profile colorboxsquare"></span>Leave / Holiday
                            </div>
                            <div class="mr-2 d-inline-block">
                                <span class="legends_high colorboxsquare"></span>Holiday / Leave Auditable Records
