                        :SortBy="SortBy" :IsShowAction="true" :ListData="computedTimesheetList" :HeaderData="Headers"
                        :showCheckBox="true" :NorecordfoundText="$t('NoRecordfound')"
                        v-on:ActionButtonClick="actionButtonClick">
                        <template v-slot:slotdata="{ data }">
                            {{ index }}
                            <template>
                                <input v-model="data.row[data.column.COLUMN_NAME]" @input="test(data, this)">
                            </template>

                        </template>
                    </tg-list>
                    <div style="float: left; width: 100%;">
                        <table style="width: 100%;">
                            <tr>

                                <td style="min-width: 100px;max-width: 100px;width: 100px;"><span></span></td>
                                <td style="min-width: 100px;max-width: 100px;width: 100px;"><span>Total</span></td>
                                <td style="min-width: 100px;max-width: 100px;width: 100px;"><span>{{ totalData[3].value
