                                <td style="min-width: 100px;max-width: 100px;width: 100px;"><span>{{ totalData[7].value
                                }}</span></td>
                                <td style="min-width: 100px;max-width: 100px;width: 100px;"><span>{{ totalData[8].value
                                }}</span></td>
                                <td style="min-width: 100px;max-width: 100px;width: 100px;"><span>{{ totalData[9].value
                                }}</span></td>
                                <td style="min-width: 100px;max-width: 100px;width: 100px;"><span>{{ totalData[3].value
                                }}</span></td>
                                <td style="min-width: 100px;max-width: 100px;width: 100px;"><span></span></td>


                            </tr>
                        </table>
