            }
        },
        methods: {
            saveTaskEntry(e) {
                debugger
                var vm = this;
                var obj = {}
                vm.Headers.map((item) => {
                    obj[item.COLUMN_NAME] = '00:00'
                })
                Object.assign(obj, e)
                vm.TimesheetList.push(obj)
                this.addTaskShow = false
            },
            showAddTaskModal() {
