                // this.Headers.forEach((column) => {
                //     //console.log('header', column)
                //     if (column.COLUMN_NAME !== 'ProjectName' && column.COLUMN_NAME !== 'TaskDetail' && column.COLUMN_NAME !== 'TaskId' && column.COLUMN_NAME !== 'ProjectId') {
                //         //console.log('column', column)
                //         this.TimesheetList.forEach((data, index) => {
                //             const [hours, minutes] = data[column.COLUMN_NAME] != null ? data[column.COLUMN_NAME].split(":") : "00:00".split(":");
                //             totalSecond += parseInt(hours) * 3600 + parseInt(minutes) * 60;
                //             sum1 += data[column.COLUMN_NAME]
                //         });
                //     }
                //     this.totalSeconds = totalSecond;
                //     totalSecond = 0;
                //     this.TimesheetList.map((data, index) => {
                //         var rowcount = 0;
                //         this.Headers.forEach((column) => {
                //             if (column.COLUMN_NAME !== 'ProjectName' && column.COLUMN_NAME !== 'TaskDetail' && column.COLUMN_NAME !== 'TaskId' && column.COLUMN_NAME !== 'ProjectId' && column.COLUMN_NAME !== 'RowTotal') {
                //                 // console.log('index', column.COLUMN_NAME + 'ff' + index)
                //                 // console.log('data', data[column.COLUMN_NAME] + 'ff' + index)
                //                 const [hours, minutes] = data[column.COLUMN_NAME] != null ? data[column.COLUMN_NAME].split(":") : "00:00".split(":");
                //                 rowcount += parseInt(hours) * 3600 + parseInt(minutes) * 60;
                //             }

                //         });
                //         const hours = Math.floor(rowcount / 3600);
                //         const minutes = Math.floor((rowcount % 3600) / 60);
                //         const formattedTime = String(hours).padStart(2, "0") + ":" + String(minutes).padStart(2, "0");

                //         data.RowTotal = formattedTime;
                //         return data;
                //     });
                //     console.log("after rowcount", this.TimesheetList);
                // });


            },
            async GetUsersForTimesheet() {
                var VueObj = this;
                VueObj.isLoading = true;
                var url = `companyId=${VueObj.GetUserInfo.CompanyId}&loggedUserId=${VueObj.GetUserInfo.ID}&userType=CA&userId=${VueObj.FormSchema[0].Data[1].value}&moduleName=`;
                await DataService.GetUsersForTimesheet(url)
                    .then(function (response) {

                        response.data.result.forEach(item => {
                            VueObj.obj = {
                                name: item.username,
                                value: item.useR_ID
                            }
                            VueObj.FormSchema[0].Data[1].config.options.push(VueObj.obj);
                        });

                        VueObj.isLoading = false;
                    });
            },
            OnChange: function () {
                var checkCount = $('.chkItems:checked').not("[id^='chkAll']").length;
                if (checkCount > 0) {
                    $('#DeleteMultiple').removeClass('disabled');
                }
                else {
                    $('#DeleteMultiple').addClass('disabled');
                }
            },
            commonHeaderButtonClick: function (event) {


            },
