                var vm = this;
                vm.FetchData();
            },
            RdatePickerForWeekRange: async function (dateFrom) {


            },
            GetStartEndDates: async function (event, id, a) {
                //console.log("date", this.dateselect)
                var VueObj = this;
                let url = `DateSpan=thisweek&report=`;
                await DataService.GetStartEndDate(url).then(response => {
                    VueObj.StartDate = response.data.dateStart;
                    VueObj.EndDate = response.data.dateEnd;
                    VueObj.DisplayWeekDate = "";
                    VueObj.DisplayWeekDate = response.data.displayText;
                    VueObj.StartWeekDay = response.data.startWeekDay;
                });
                //VueObj.FormSchema[0].Data[0].placeholder = "Date Range: " + VueObj.DisplayWeekDate;
                var offset = 1;
                switch (VueObj.StartWeekDay.toLowerCase()) {
                    case 'tuesday':
                        offset = 2;
                        break;
                    case 'wednesday':
                        offset = 3;
                        break;
                    case 'thursday':
                        offset = 4;
                        break;
                    case 'friday':
                        offset = 5;
                        break;
                    case 'satureday':
                        offset = 6;
                        break;
                    case 'sunday':
                        offset = 0;
                        break;
                    default:
                        offset = 1;
                        break;
                }


                // switch (VueObj.StartWeekDay) {
                //     case "wednesday":
                //         offset = 4;
                //         break;
                //     case "sm":
                //         limitSize = 1;
                //     case "md":
                //         limitSize = 2;
                //     case "lg":
                //         limitSize = 3;
                //     case "xl":
                //         limitSize = 3;
                // }


                var today = new Date(this.dateselect);
                if (today == '') {
                    today = new Date();
                }
                var currentDay = today.getDay() - offset;
                var startdate = new Date(today.getFullYear(), today.getMonth(), today.getDate() - currentDay);
                var enddate = new Date(today.getFullYear(), today.getMonth(), today.getDate() - currentDay + 6);
                var dateTime = VueObj.$options.filters.formatDate(startdate) + '-' + VueObj.$options.filters.formatDate(enddate);

                VueObj.StartDate = VueObj.$options.filters.formatDate(startdate);
                VueObj.EndDate = VueObj.$options.filters.formatDate(enddate);
                VueObj.DisplayWeekDate = "";
                VueObj.DisplayWeekDate = dateTime;

                //VueObj.FetchData();

            },
            GetStartEndDatesNew: async function (thisweek) {
                var VueObj = this;
                let url = `DateSpan=thisweek&report=`;
                await DataService.GetStartEndDate(url).then(response => {
                    VueObj.StartDate = response.data.dateStart;
                    VueObj.EndDate = response.data.dateEnd;
                    VueObj.DisplayWeekDate = response.data.displayText;
                    VueObj.StartWeekDay = response.data.startWeekDay;
                });
                VueObj.FormSchema[0].Data[0].placeholder = "Date Range: " + VueObj.DisplayWeekDate;
                VueObj.DisplayWeekDate = "This Week";

            },
            FetchData: async function () {
                var VueObj = this;
                VueObj.isLoading = true;
                var fDate = VueObj.StartDate;
                var tDate = VueObj.EndDate;
                var inputData = []
                // var url = `userId=${VueObj.GetUserInfo.ID}&dateFrom=${fDate}&dateTo=${tDate}&userId=${VueObj.FormSchema[0].Data[1].value}&companyId=270936`;
                var url = `companyId=${VueObj.GetUserInfo.CompanyId}&userId=${VueObj.FormSchema[0].Data[1].value}&dateFrom=${fDate}&dateTo=${tDate}`;
                await DataService.ListTimesheetForWeek(url).then(response => {
                    if (response.data != null && response.data != "" && response.data.length > 0) {

                        // const headerList=Object.keys(response.data[0]).filter(key => key !== 'ProjectId' && key !== 'ProjectName' && key !== 'TaskId' && key !== 'TaskDetail');
                        const headerList = Object.keys(response.data[0]);
                        VueObj.isLoading = false;
                        var count;
                        this.Headers = [];
                        headerList.forEach(item => {
                            count++;
                            if (item == "ProjectId" || item == "TaskId") {
                                this.Headers.push({
                                    "COLUMN_NAME": item,
                                    "DATA_TYPE": "string",
                                    "SORTABLE": false,
                                    "VISIBLE": false,
                                    "DISPLAY_NAME": item,
                                    "DISPLAY_ORDER": count,
                                });
                            }
                            else if (item == "TaskDetail") {
                                // this.Headers.push({
                                //     "COLUMN_NAME": item,
                                //     "DATA_TYPE": "string",
                                //     "SORTABLE": false,
                                //     "VISIBLE": true,
                                //     "DISPLAY_NAME": item,
                                //     "DISPLAY_ORDER": count,
                                // });
                            }
                            else if (item == "ProjectName") {
                                this.Headers.push({
                                    "COLUMN_NAME": item,
                                    "DATA_TYPE": "string",
                                    "SORTABLE": false,
                                    "VISIBLE": true,
                                    "DISPLAY_NAME": "Projects",
                                    "DISPLAY_ORDER": 1
                                });
                            }
                            else {
                                this.Headers.push({
                                    "COLUMN_NAME": item,
                                    "DATA_TYPE": "text",
                                    "SORTABLE": false,
                                    "VISIBLE": true,
                                    "DISPLAY_NAME": getPreviousDay(item),
                                    "DISPLAY_ORDER": count,
                                });
                            }
                        });

                        this.Headers.push({
                            "COLUMN_NAME": "RowTotal",
                            "DATA_TYPE": "string",
                            "SORTABLE": false,
                            "VISIBLE": true,
                            "DISPLAY_NAME": "RowTotal",
                            "DISPLAY_ORDER": count,
                        });
                        // headerList.forEach((item) => {
                        //     VueObj.Headers.forEach(column => {
                        //         column.settings = null;

                        //         if (column.COLUMN_NAME == item && column.COLUMN_NAME !=='ProjectName' && item !=='ProjectName') {
                        //             column.settings = {
                        //                 //isInSlot: true
                        //             }
                        //         }
                        //     }
                        //     );
                        // });

                        VueObj.Headers.forEach((column) => {
                            column.settings = null;
                            headerList.forEach(item => {

                                if (column.COLUMN_NAME == item && column.COLUMN_NAME !== 'ProjectName' && column.COLUMN_NAME !== 'TaskDetail') {
                                    column.settings = {
                                        isInSlot: true
                                    }
                                }
                            }
                            );
                        });

                        VueObj.TotalRecords = response.data.length;
                        VueObj.TimesheetList = response.data;
                        var scol = 0;
                        var totalSeconds = 0;
                        VueObj.TimesheetList.forEach((item, index) => {
                            VueObj.Headers.forEach((column) => {
                                if (column.COLUMN_NAME !== 'ProjectName' && column.COLUMN_NAME !== 'TaskDetail' && column.COLUMN_NAME !== 'TaskId' && column.COLUMN_NAME !== 'ProjectId') {
                                    //console.log(item[column.COLUMN_NAME]);
                                    inputData[index + column.COLUMN_NAME] = item[column.COLUMN_NAME]
                                }


                            })
                            //inputData['row'+index+'col1'] = item.

                            Object.entries(item).reduce((filtered, [key, value]) => {
                                if (/^\d{1,2}\/\d{1,2}\/\d{4}$/.test(key)) {
                                    scol += value;
                                    const [hours, minutes] = value != null ? value.split(":") : "00:00".split(":");
                                    totalSeconds += parseInt(hours) * 3600 + parseInt(minutes) * 60;
                                    //return totalSeconds;
                                }
                                //return filtered;
                            }, {});

                            Object.entries((totalMinutes, time) => {
                                const [hours, minutes] = time.split(":");
                                return totalMinutes + parseInt(hours) * 60 + parseInt(minutes);
                            }, 0);

                        });
                        //console.log("inputData", inputData)
                        //VueObj.formData = inputData
                        VueObj.SumRowColTotalList()
                        // this.TimesheetList.reduce((totalMinutes, time) => {
                        //     const [hours, minutes] = time.split(":");
                        //     return totalMinutes + parseInt(hours) * 60 + parseInt(minutes);
                        // }, 0);

                        // Object.entries(VueObj.TimesheetList).reduce((filtered, [key, value]) => {
                        //     if (/^\d{1,2}\/\d{1,2}\/\d{4}$/.test(key)) {
                        //         filtered[key] = value;
                        //     }
                        //     return filtered;
                        // }, {});

                        // var sum = 0;
                        // VueObj.TimesheetList.reduce(function (item) {
                        //     return sum + item.amount;
                        // }, 0);

                        VueObj.TotalPages = 1;
                        VueObj.CurrentPage = 1;
                        $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                        $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                        VueObj.noRecord = false;

                        $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                        $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                        VueObj.noRecord = false;
                    }
                    else {
                        VueObj.noRecord = true;
                        VueObj.TimesheetList = [];
                    }

                });
            },
            // FetchRowColTotalList:async function() {
            SumRowColTotalList: async function () {
                var VueObj = this;
                VueObj.totalData = [];
                //var sum1 = 0;
                VueObj.Headers.forEach((column) => {
                    var colcount = 0;
                    //console.log('header', column)
                    if (column.COLUMN_NAME !== 'ProjectName' && column.COLUMN_NAME !== 'TaskDetail' && column.COLUMN_NAME !== 'TaskId' && column.COLUMN_NAME !== 'ProjectId') {
                        //console.log('column', column)
                        VueObj.TimesheetList.forEach((data, index) => {
                            //scol += value;
                            const [hours, minutes] = data[column.COLUMN_NAME] != null ? data[column.COLUMN_NAME].split(":") : "00:00".split(":");
                            colcount += parseInt(hours) * 3600 + parseInt(minutes) * 60;
                            //sum1 += data[column.COLUMN_NAME]
                            // console.log('data1', data[column.COLUMN_NAME])
                            // console.log('sum1', sum1)

                        });
                        //console.log('colcount', colcount)
                        const hours = Math.floor(colcount / 3600);
                        const minutes = Math.floor((colcount % 3600) / 60);
                        var formattedTime = String(hours).padStart(2, "0") + ":" + String(minutes).padStart(2, "0");

                    }
                    VueObj.totalData.push({ value: formattedTime });
                    VueObj.totalSeconds = colcount;
                    //totalSecond = 0;
                    VueObj.TimesheetList.map((data, index) => {
                        var rowcount = 0;
                        VueObj.Headers.forEach((column) => {
                            if (column.COLUMN_NAME !== 'ProjectName' && column.COLUMN_NAME !== 'TaskDetail' && column.COLUMN_NAME !== 'TaskId' && column.COLUMN_NAME !== 'ProjectId' && column.COLUMN_NAME !== 'RowTotal') {
                                // console.log('index', column.COLUMN_NAME + 'ff' + index)
                                // console.log('data', data[column.COLUMN_NAME] + 'ff' + index)
                                const [hours, minutes] = data[column.COLUMN_NAME] != null ? data[column.COLUMN_NAME].split(":") : "00:00".split(":");
                                rowcount += parseInt(hours) * 3600 + parseInt(minutes) * 60;
                            }

                        });
                        const hours = Math.floor(rowcount / 3600);
                        const minutes = Math.floor((rowcount % 3600) / 60);
                        const formattedTime = String(hours).padStart(2, "0") + ":" + String(minutes).padStart(2, "0");

                        data.RowTotal = formattedTime;
                        //console.log(formattedTime);
                        return data;
                    });
                    // console.log("after rowcount 1st time", VueObj.TimesheetList);
                });
                console.log("totalData", VueObj.totalData);

                console.log("after rowcount 1st time", VueObj.TimesheetList);
                //VueObj.FetchRowColTotalList();

            },
