            renderActions: function (row) {
                var self = this;
                let rtnActionArray = [];
                self.listActions.forEach(actionItem => {
                    let action = {
                        ...actionItem
                    };
                    switch (action.title) {
                        case 'Edit':
                            action.isVisible = true;
                            break;
                        case 'Delete':
                            action.isVisible = true;
                            break;
                        default:
                            action.isVisible = true;
                            break;
                    }
                    rtnActionArray.push(action);
                });
